*,
*::before,
*::after {
  box-sizing: border-box;
}

button,
input {
  all: unset;
}

@font-face {
  font-family: Montserrat;
  src: url(./fonts/Montserrat-Regular.otf);
}
@font-face {
  font-family: Roboto;
  src: url(./fonts/Roboto-Medium.ttf);
}
h1,
h2,
h3 {
  text-align: center;
}

a {
  text-decoration: none;
}

a:hover {
  color: #ff7043;
}

button {
  font-family: Roboto;
  text-align: center;
  padding: 0.5rem 2rem;
  text-transform: uppercase;
  box-shadow: 0 0 0.4rem 0 rgba(0, 0, 0, 0.5);
  cursor: pointer;
  transition: outline-offset 200ms ease;
  transition: background-color 200ms ease-out;
}
button.primary {
  background-color: #424242;
  color: #f0f0f0;
}
button.primary:hover, button.primary:focus {
  background-color: #4f4f4f;
}
button.primary:active {
  background-color: #292929;
}
button.secondary {
  background-color: #546e7a;
  color: #f0f0f0;
}
button.secondary:hover, button.secondary:focus {
  background-color: #5e7c89;
}
button.secondary:active {
  background-color: #3f535c;
}

input {
  box-sizing: inherit;
  background-color: #333;
  color: #f0f0f0;
  padding: 0.6rem;
  margin: 1rem auto;
  width: 100%;
  transition: outline 600ms ease;
}
input:focus {
  outline: 0.1rem solid #546e7a;
}
input#msg {
  margin-right: 0.2rem;
}

footer {
  width: 10%;
  max-width: 10%;
  margin: auto;
  text-align: center;
}
footer img {
  background-color: #424242;
  padding: 1rem;
  border-radius: 52% 48% 32% 68%/83% 43% 57% 17%;
  transition: background-color 250ms ease-out;
}
footer img:hover {
  background-color: #546e7a;
}

.container {
  padding: 1rem;
  background-color: #424242;
  color: #f0f0f0;
  width: 30rem;
  max-width: 90%;
  margin: auto;
  box-shadow: 0 0 0.4rem 0 rgba(0, 0, 0, 0.8);
}
.container.message-box {
  height: 95%;
  justify-content: space-between;
  position: relative;
}
.container.message-box .connected-as {
  font-size: 0.8rem;
  position: absolute;
  bottom: 0.3rem;
  background-color: #424242;
}
.container.message-box .connected-as strong {
  color: darkseagreen;
}

.card.welcome {
  text-align: center;
  padding: 1rem 2rem;
}
.card.messages {
  background-color: #35454d;
  outline: 1px solid #546e7a;
  color: #f0f0f0;
  height: 85%;
  max-height: 85%;
  overflow-y: scroll;
}
.card.messages ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
.card.messages ul li {
  padding: 1rem;
  justify-content: left;
}
.card.messages ul li:nth-of-type(even) {
  background-color: #353535;
}
.card.messages ul li div.name {
  align-self: flex-start;
  margin-right: 0.5rem;
}
.card.messages ul li div.message {
  word-break: break-all;
}

.flex-column {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.flex-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.self-right {
  align-self: flex-end;
}

body {
  background-color: #e0e0e0;
  font-family: Montserrat;
  line-height: 1.5;
}

main {
  height: 33rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
